import React, { FC } from "react";
import { Link } from "react-router-dom";
import clsx from "clsx";

import { ThemeEvents } from "providers";

import css from "./style.module.scss";
import { eventImgsList } from "./imagesList";

interface Props
  extends React.DetailedHTMLProps<
    React.ImgHTMLAttributes<HTMLImageElement>,
    HTMLImageElement
  > {
  eventName: ThemeEvents;
}

const EventLogo: FC<Props> = ({ eventName, ...props }) => {
  return (
    <Link to="./">
      <img
        {...props}
        src={eventImgsList[eventName]}
        alt=""
        className={clsx(css.root, css[eventName], props.className)}
      />
    </Link>
  );
};

export default EventLogo;
